body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@charset "UTF-8";
@font-face {
  font-family: 'webfont';
  font-display: swap;
  src: url("//at.alicdn.com/t/webfont_yqn92wjzqr.eot");
  /* IE9*/
  src: url("//at.alicdn.com/t/webfont_yqn92wjzqr.eot?#iefix") format("embedded-opentype"), url("//at.alicdn.com/t/webfont_yqn92wjzqr.woff2") format("woff2"), url("//at.alicdn.com/t/webfont_yqn92wjzqr.woff") format("woff"), url("//at.alicdn.com/t/webfont_yqn92wjzqr.ttf") format("truetype"), url("//at.alicdn.com/t/webfont_yqn92wjzqr.svg#庞门正道标题体2") format("svg");
  /* iOS 4.1- */ }

.App {
  text-align: center;
  color: #fedcba; }

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 15px;
  line-height: 25px; }

th {
  background-color: #eee; }

td,
th {
  text-align: center; }

td:first-child {
  text-align: left; }

input {
  min-width: 300px;
  border: 1px solid #999;
  border-radius: 2px;
  line-height: 25px; }

#main {
  display: flex;
  justify-content: flex-end; }
  #main #merge {
    width: 100%; }
  #main .box {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 10px; }
    #main .box .btns {
      display: flex;
      justify-content: flex-start;
      align-items: center; }
    #main .box .btn {
      margin-left: 10px; }
    #main .box div {
      margin-left: 10px; }
  #main .option {
    margin-top: 10px; }
    #main .option .option-li {
      margin-top: 10px;
      border: 1px solid #eee;
      padding: 0 20px 10px;
      margin-left: 10px; }
    #main .option .row {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      margin-top: 10px; }
      #main .option .row .h3 {
        color: #333;
        font-size: 16px;
        width: 100px;
        margin-right: 10px; }
      #main .option .row .ant-btn {
        margin-right: 10px; }
      #main .option .row .ant-input {
        width: 200px;
        min-width: auto; }
  #main .example {
    width: 400px;
    min-height: 300px;
    background: #eeeeee; }

